 } */

.dataResult-header .dataItem-header {
  width: 100%;
  height: 30px;
  display: flex;
  align-items: center;
  color: black;
  font-size: smaller;
}

.dataItem-header p {
  margin-left: 10px;
}

.dataResult-header :hover {
  background-color: #ebeeef;
}
